










































































import fIcon from '@/components/m-icon/f-icon.vue'
import mpWeixin from '@/static/data/mp-weixin.js';
import {mapState,mapGetters,mapMutations} from 'vuex'

export default {
	components: {fIcon},
	data() {
		return{
			from:'',
			outTradeNo:'',
			appointments:[{
				title:'非会员',
				price:100,
				num:1,
				unit:'人',
				tagPrice:'￥100',
				save:'无优惠',
				tip:false,
				picked:false,
				disabled:true
			},{
				title:'月会员',
				price:380,
				num:5,
				unit:'人',
				tagPrice:'￥500',
				save:'立省120元',
				tip:false,
				picked:true,
				disabled:false
			},{
				title:'年会员',
				price:1314,
				num:20,
				unit:'人',
				tagPrice:'￥2000',
				save:'立省686元',
				tip:true,
				picked:false,
				disabled:false
			}],
			activities:[{
				title:'非会员',
				price:100,
				num:1,
				unit:'场',
				tagPrice:'￥100',
				save:'无优惠',
				tip:false,
				picked:false,
				disabled:true
			},{
				title:'月会员',
				price:380,
				num:5,
				unit:'场',
				tagPrice:'￥500',
				save:'立省120元',
				tip:false,
				picked:true,
				disabled:false
			},{
				title:'年会员',
				price:1314,
				num:20,
				unit:'场',
				tagPrice:'￥2000',
				save:'立省686元',
				tip:true,
				picked:false,
				disabled:false
			}]
		}
	},
	computed: {
		...mapState(['account']),
		...mapGetters(['agentMember'])
	},
	methods:{
		//...mapMutations(['saveMemberTags']),
		pick:function(list, item, index){
			if(index == 0)return;
			if(item.picked){
				item.picked = false;
				return;
			}
			list.forEach(entity=>{
				if(entity.picked){
					entity.picked = false;
				}
			})
			item.picked = true;
		},
		getOrderData:function(){
			let items = [];
			let total = 0;
			this.appointments.forEach(entity=>{
				if(entity.picked){
					items.push({
						serviceName:'appointment',
						num:entity.num,
						subtotal:entity.price
					});
					total += entity.price;
				}
			})
			this.activities.forEach(entity=>{
				if(entity.picked){
					items.push({
						serviceName:'activity',
						num:entity.num,
						subtotal:entity.price
					});
					total += entity.price;
				}
			})
			return {
				memberId:this.agentMember.id,
				accountId:this.account.id,
				agentAccountId:this.agentMember.agentAccountId,
				total:total*100,					//转化为分
				items:JSON.stringify(items)
			}
		},
		submit:function(){
			let data = this.getOrderData();
			if(data.total == 0){
				uni.showToast({
					icon:'none',
					title:'请选择开通的会员'
				})
				return;
			}
			
			//测试代码
			data.total = 1;
			
			//先保存，返回商户订单号
			this.preSaveOrder(data);
		},
		preSaveOrder:function(data){	//预先保存会员订单
			let that = this;
			uni.request({
				url:this.$http.contextPath + 'member/order/save',
				method:'POST',
				data:data,
				success(res) {
					if(that.$http.success(res)){
						that.outTradeNo = res.data.data.outTradeNo;




						uni.navigateTo({
							url:'/pages/payment/payment?from='+that.from+'&outTradeNo='+res.data.data.outTradeNo
						})

					}else{
						uni.showToast({
						    icon: 'none',
						    title: '下单失败，请稍后再试'
						});
					}
				}
			})
		},
		saveOrder:function(data, outTradeNo){	//支付成功后，保存会员订单
			let that = this;
			that.outTradeNo = outTradeNo;
			data.outTradeNo = outTradeNo;
			uni.request({
				url:this.$http.contextPath + 'member/order/save',
				method:'POST',
				data:data,
				success(res) {
					if(that.$http.success(res)){
						uni.showModal({
							title: '提示',
							content: '支付成功！请确认返回',
							showCancel:false,
							success: function (res) {
								if (res.confirm) {
									uni.navigateBack();
								} 
							}
						})
						// window.location.href = that.$http.contextPath + 'payment/common/index?outTradeNo='+res.data.data.outTradeNo
					}else{
						uni.showToast({
						    icon: 'none',
						    title: '下单失败，请稍后再试'
						});
					}
				}
			})
		},
		payment:function(data){
			console.log("发起支付");
			let that = this;
			uni.login({
				success: (e) => {
					uni.request({
						url: mpWeixin.openIdUrl + e.code,
						method: 'GET',
						success: (sess) =>{
							console.log("获取openId");
							console.log(sess.data.openid);
							console.log("报名费: " + data.total);
							//
							uni.request({
								url: mpWeixin.orderUrl, //统一下单
								method:'POST',
								 header: {
									"Content-Type": "application/x-www-form-urlencoded"  // 默认值
								},
								data:{
									body: '阅见会员费',
									total_fee: data.total,
									trade_type: 'JSAPI',
									openid: sess.data.openid
									// out_trade_no: "test"+String.valueOf(new Date().getTime())
								},
								success: (res) => {
									console.log("pay request success", res);
									if (res.statusCode !== 200) {
										uni.showModal({
											content: "支付失败，请重试！",
											showCancel: false
										});
										return;
									}
									uni.requestPayment({
										timeStamp: res.data.timeStamp,
										nonceStr: res.data.nonceStr,
										package: res.data.package,
										signType: 'MD5',
										paySign: res.data.paySign,
										success: () => {
											that.saveOrder(data, res.data.out_trade_no);
										},
										fail: (res) => {
											uni.showModal({
												content: "支付失败,原因为: " + res
													.errMsg,
												showCancel: false
											})
										},
										complete: () => {
											// this.loading = false;
										}
									})
								},
								fail: (e) => {
									console.log("fail", e);
									// this.loading = false;
									uni.showModal({
										content: "支付失败,原因为: " + e.errMsg,
										showCancel: false
									})
								}
							})
						}
					});
				},
				fail: (e) => {
					console.log("fail", e);
					// this.loading = false;
					uni.showModal({
						content: "支付失败,原因为: " + e.errMsg,
						showCancel: false
					})
				}
			})
		},
	},
	onLoad(option) {
		this.from = option.from;
	}
}
